import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Navbar, Footer } from "../../components"
import { Header, Play, Team, Community } from "../Home"

export default function Home() {
  function ServerStatus() {
    const [serverStatus, setServerStatus] = useState('Loading');

    useEffect(() => {
      // Make a request to the server
      axios.get('http://play.mythosmc.com')
        .then(() => {
          // If the request is successful, set the status to 'Online'
          setServerStatus('Online');
        })
        .catch(() => {
          // If the request fails, set the status to 'Offline'
          setServerStatus('Offline');
        });
    }, []);
  
    return (
      <div>
        <h1>Server Status: {serverStatus}</h1>
      </div>
    );
  }

  return (
    <div className='mc__home'>
        <Navbar />
        <Header />
        <ServerStatus />
        <Play />
        <Community />
        <Team />
        <Footer />
    </div>
  )
}
